import React, { useState, useEffect } from 'react'
import { Link, useStaticQuery, graphql, navigate } from 'gatsby'

const Layout = ({ pageTitle, children }) => {

  const data = useStaticQuery(graphql`
    query {
      site {
        siteMetadata {
          title
          description
        }
      }
    }
  `)
  let paramSearch = '';
  if (typeof window !== 'undefined') {
    const params = new URLSearchParams(window.location.search);
    paramSearch = params.get("param") || '';
  } else {
    paramSearch = '';
  }

  const [param, setParam] = useState(paramSearch)

  const handleChange = (event) => {
    setParam(event.target.value);
    console.log(param, event.target.value)
  }

  const doSearch = () => {

    fetch(`https://jsonplaceholder.typicode.com/todos?userId=${param}`)
      .then(response => response.json()) // parse JSON from request
      .then(resultData => {
        navigate('/search' + '?param=' + param, {
          state:{
            data: resultData
          }
        });
      })
  }

  return (
    <main >
      <div id="header">
        <div className="toolbar-inside">
              <div className="header-container">
                <div className="header-container-left">
                    <div className="toolbar-logo toolbar-subMenu-box header-fl">
                        <Link to="/">
                            <img width="24px" height="24px" title="shortpage" src="https://trangianhuan.github.io/icon/awesome-icons/svg/user-shape.svg" />
                        </Link>
                    </div>

                </div>
                <div className="header-container-middle">
                    <div className="toolbar-search onlySearch">
                        <div className="toolbar-search-container">
                            <input id="toolbar-search-input" autoComplete="off"
                              type="text"
                              placeholder="search"
                              value={param}
                              onChange={handleChange}/>
                            <button id="toolbar-search-button" onClick={() => doSearch()}></button>
                        </div>
                    </div>
                </div>
                <div className="header-container-right">
                <div className="toolbar-btns onlyUser"></div>
                </div>
              </div>
        </div>
      </div>
      <div className="container">
        {children}
      </div>
    </main>
  )
}

export default Layout
