import React, { useState, useEffect } from 'react'
import Layout from '../components/layout'
import { Link } from 'gatsby'
import { graphql } from "gatsby"



const SearchPage = (props) => {
  console.log(props)
  const params = new URLSearchParams(props.location.search);
//   const parameter1 = params.get("param");
  // const books = props.data.allMongodbVibloPosts.edges;
  const [posts, setPosts] = useState([])
  const [parameter1, setParameter1] = useState('')
  console.log(parameter1, params.get("param"))

//   if (parameter1 != params.get("param")) {
//     setPosts([])
//     console.log('aaaaa')
//     setParameter1(params.get("param"))
//   }

  useEffect(() => {
    // get data from GitHub api
    fetch(`https://jsonplaceholder.typicode.com/todos?userId=${parameter1}`)
      .then(response => response.json()) // parse JSON from request
      .then(resultData => {
        console.log(resultData)
        setPosts(resultData)
        setParameter1(params.get("param"))
      })
  }, [])

  return (
    <Layout pageTitle="Blogs ">
      search page
    </Layout>
  )
}

export default SearchPage
